/* Placeholder Web Fonts */
@import url("https://fonts.googleapis.com/css?family=Nunito+Sans");
/*
  Web Font Imports
 */
@import url(https://fonts.googleapis.com/earlyaccess/notosanstc.css);
/*
  Colours
 */
/*
  Font Sizes
  3 sections by width, h1~h6 + p
 */
/*
  Mixins
 */
/*
  Generic, Strucutral Styling
 */
::-webkit-scrollbar {
  width: 0;
}
.persistent {
  transform: translate3d(0, 0, 0);
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.persistent .float {
    position: relative;
    outline: 1px solid #008080;
    height: calc(100vh - 64px);
    max-width: calc(100vw - 64px);
    min-width: calc(100vw - 64px);
    padding: 32px;
}
.persistent .float .onCorners {
      position: absolute;
}
.persistent .float .topLeft {
      top: 32px;
      left: 32px;
}
.persistent .float .topRight {
      top: 32px;
      right: 32px;
}
.persistent .float .bottomLeft {
      bottom: 32px;
      left: 32px;
}
.persistent .float .bottomRight {
      bottom: 32px;
      right: 32px;
}
.page-section {
  position: relative;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.page-section .content {
    position: relative;
    outline: 1px solid #008080;
    height: calc(100vh - 64px);
    max-width: calc(100vw - 64px);
    min-width: calc(100vw - 64px);
    padding: 32px;
}
.page-section .content .onCorners {
      position: absolute;
}
.page-section .content .topLeft {
      top: 32px;
      left: 32px;
}
.page-section .content .topRight {
      top: 32px;
      right: 32px;
}
.page-section .content .bottomLeft {
      bottom: 32px;
      left: 32px;
}
.page-section .content .bottomRight {
      bottom: 32px;
      right: 32px;
}

/*
  Event-related CSS
 */
body.stopScrolling {
  overflow: hidden;
}

/* Global Styling */
body {
  margin: 0;
  padding: 0;
  font-family: "Noto Sans TC", "Avenir Next", "Helvetica", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Transitions */
.page-fade-enter-active, .page-fade-leave-active {
  transition: 0.3s cubic-bezier(0.33, 0.85, 0.85, 0.99);
}
.page-fade-enter {
  opacity: 0;
}
.page-fade-leave-to {
  opacity: 0;
}



